<template>
  <div class="pagetitle">
    <h1 class="text-white text-center">틀린그림찾기</h1>
  </div>
  <b-container class="p-4 bg-dark text-center">
    <b-row>
      <!-- img1 -->
      <b-col>
        <b-img
          id="img1"
          thumbnail
          fluid
          :src="img1Src"
          alt="Original Image"
        ></b-img>
      </b-col>
    </b-row>
    <b-row class="mt-4">
      <!-- img2 (Canvas) -->
      <b-col>
        <canvas id="canvas2" width="250" height="250"></canvas>
      </b-col>
    </b-row>
    <b-row class="mt-4">
      <!-- Button -->
      <b-col>
        <b-button class="mt-4" variant="primary" @click="fetchAndAlterImage">
          이미지 변경하기
        </b-button>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
export default {
  data() {
    return {
      img1Src: "", // 초기 이미지
    };
  },
  methods: {
    // 랜덤 이미지 가져오기 및 변조 적용
    async fetchAndAlterImage() {
      try {
        const response = await fetch(
          "https://api.unsplash.com/photos/random?query=disney-animation&client_id=tS5JxTo6QVRLqKK5npt5AWY7Hgmht3q9njItgSmN9tM"
        );
        const data = await response.json();
        this.img1Src = data.urls.small; // img1Src 업데이트
        this.applyAlteration(data.urls.small); // img2 변조 처리
      } catch (error) {
        console.error("이미지를 가져오는 중 오류가 발생했습니다.", error);
      }
    },
    // Canvas를 사용하여 변조 작업 수행
    applyAlteration(imageSrc) {
      const canvas = document.getElementById("canvas2");
      const ctx = canvas.getContext("2d");

      const img = new Image();
      img.crossOrigin = "anonymous"; // 크로스오리진 설정
      img.src = imageSrc;

      img.onload = () => {
        // Canvas에 원본 이미지 그리기
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);

        // 변조 영역 크기
        const alterationSize = 50;
        const x = Math.random() * (canvas.width - alterationSize);
        const y = Math.random() * (canvas.height - alterationSize);

        // 변조 작업
        const choice = Math.random();
        if (choice < 0.5) {
          this.blurRegion(ctx, x, y, alterationSize); // 흐림 처리
        } else {
          this.removeRegionSmoothly(ctx, x, y, alterationSize); // 자연스러운 제거 처리
        }
      };
    },
    // 특정 영역 흐림 처리
    blurRegion(ctx, x, y, size) {
      ctx.save();
      ctx.filter = "blur(5px)"; // 블러 필터 적용
      ctx.drawImage(
        ctx.canvas,
        x,
        y,
        size,
        size,
        x,
        y,
        size,
        size
      ); // 기존 이미지 위에 흐림 처리
      ctx.restore();
    },
    // 특정 영역 자연스럽게 제거 처리
    removeRegionSmoothly(ctx, x, y, size) {
      // 캔버스에서 주변 영역 가져오기
      const surrounding = ctx.getImageData(x - size / 2, y - size / 2, size, size);

      // 주변 데이터를 약간 이동시켜서 채우기
      ctx.putImageData(surrounding, x, y);
    },
  },
};
</script>

<style scoped>
.pagetitle {
  padding: 20px;
  background-image: url('https://images.unsplash.com/photo-1472289065668-ce650ac443d2?q=80&w=869&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
  background-repeat: no-repeat;
  background-position: center;
}

canvas {
  border: 1px solid #ddd;
}
</style>
